.page-students {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 180px;
    grid-template-rows: auto auto 1fr;
}

.students__header {
    display: flex;
    justify-content: space-between;
    padding-bottom: 18px;
    grid-column: 1/3;
}

.btn-addStudent {
    font-weight: 500;
    padding: 0 30px;
}

.filterHadler {
    font-size: 20px;
    cursor: pointer;
}

.filter__form {
    display: flex;
    align-items: flex-end;
    max-height: 0;
    overflow-y: hidden;
    padding: 0 5px 5px 5px;
    transition: max-height 0.2s ease-out;
}

.showFilter__form {
    max-height: 67px;
}

.filter-BlockInput {
    margin-right: 20px;
    flex-shrink: 0;
    flex-grow: 1;
    flex-basis: auto;
}

.custom-input:focus {
    box-shadow: 0 0 0 0.25rem rgba(241, 128, 36, 0.25);
    border: 1px solid #F6B37B;
}

.btn-custom {
    border-radius: 30px;
    background-color: #f18024;
    border-color: #f18024;
    color: white;
}

.btn-custom:hover {
    background-color: #DE6B0E;
    color: white;
}

.btn-custom:focus {
    box-shadow: 0 0 0 0.25rem rgba(241, 128, 36, 0.25)
}

.btn-sumbit {
    margin-right: 10px;
}

.line {
    margin-top: 10px;
    background-color: 1px solid #d7d7d7;
}

.table-divided {
    border-spacing: 0 10px;
    border-collapse: separate;
    grid-row: -2/-1;
}

.table-divided td {
    background-color: white;
    padding: 10px;
    vertical-align: middle;
}

/* 0 1px 15px rgb(0 0 0 / 4%), 0 1px 6px rgb(0 0 0 / 4%); */

.notFoundOrEmpty {
    color: #f18024;
    padding: 20px;
    background-color: rgba(241, 128, 36, .2);
    border-color: rgba(241, 128, 36, .1);
    grid-row: -2/-1;
}

@media screen and (max-width:992px) {
    .page-students {
        grid-template-columns: 1fr;
    }
}

@media screen and (max-width:857px){
    .filter__form{
        flex-direction: column;
        align-items: stretch;
        gap:10px;
        padding: 0;
        transition: max-height 0.7s ease-out;
    }
    .showFilter__form {
        max-height: 220px;
    }
    .filter-BlockInput{
        margin-right: 0;
    }
   
}