.courses__header {
    display: flex;
    padding-bottom: 10px;
    justify-content: space-between;
    align-content: center;
    border-bottom: 1px solid #D7D7D7;
}

.EmptyCourse {
    color: #f18024;
    padding: 20px;
    background-color: rgba(241, 128, 36, .2);
    border-color: rgba(241, 128, 36, .1);
}

.cards {
    display: flex;
    flex-wrap: wrap;
    gap: 30px;
    margin-top: 30px;
}

.card {
    box-shadow: 0 1px 15px rgb(0, 0, 0, 4%), 0 1px 6px rgb(0, 0, 0, 4%);
    border-color: none;
}

.card:hover {
    box-shadow: 0 5px 10px rgb(0, 0, 0, 20%);
}

.card__img {
    background-image: url(../../images/card-img.png);
    background-size: cover;
    width: 300px;
    height: 200px;
    text-align: center;
    position: relative;
}

.card__title {
    position: absolute;
    color: white;
    top: 30%;
    left: 50%;
    transform: translateX(-50%);
}

.card__body {
    padding: 28px;
}