.modal {
    position: fixed;
    right: -400px;
    top: 0;
    bottom: 0;
    z-index: 100;
    width: 380px;
    visibility: hidden;
    opacity: 0;
    transition: all 0.5s linear;
    background-color: white;
}

.modalShow {
    right: 0;
    opacity: 1;
    visibility: visible;
}

.modalHeader {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid rgb(243, 243, 243);
    padding: 28px;
}

.modalHeader>i {
    font-size: 20px;
    cursor: pointer;
}

.modalBody {
    display: flex;
    flex-direction: column;
    padding: 28px;
}

.customInput:focus {
    box-shadow: 0 0 0 0.25rem rgba(241, 128, 36, 0.25);
    border: 1px solid #F6B37B;
}

.customInput:valid {
    border: 1px solid rgb(0, 128, 0);
    background-color: rgba(59, 245, 59, 0.18);
}

.customInput:valid:focus {
    box-shadow: 0 0 0 0.25rem rgba(0, 128, 0, 0.25);
}

.customRadioButton:checked {
    background-color: #f18024;
    border-color: #f18024;
    box-shadow: 0 0 0 0.25rem rgba(241, 128, 36, 0.25);
}

.customSelect:focus {
    box-shadow: 0 0 0 0.25rem rgba(241, 128, 36, 0.25);
    border-color: #F6B37B;
}

.btnCustom {
    border-radius: 30px;
    background-color: #f18024;
    border-color: #f18024;
    color: white;
}

.btnCustom:hover {
    background-color: #DE6B0E;
    color: white;
}

.btnCustom:focus {
    box-shadow: 0 0 0 0.25rem rgba(241, 128, 36, 0.25)
}