.top-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 30px;
    background-color: white;
    height: 80px;
    box-shadow: 0 1px 15px rgb(0 0 0 / 4%), 0 1px 6px rgb(0 0 0 / 4%);
    position: fixed;
    width: 100%;
    z-index: 99;
}

.nav-left {
    flex-basis: 40%;
    display: flex;

}

.topBar__menu{
    background: none;
    border: none;
    display: none;

}
.topBar__menu:active{
    transform: scale(0.9)
}



.topBar__logo {
    height: 80px;
}

.nav-right {
    flex-basis: 40%;
    text-align: right;
    
}

.btn-fullscreen {
    border: none;
    background: none;
    color: #8f8f8f;
    margin-right: 5px;
}

.btn-fullscreen:hover {
    color: #F18024;
}

.avatar{
    border-radius: 30px;
    width: 45px;
    margin-bottom: 5px;
    margin-left: 10px;
    margin-right: 10px;
}

@media screen and (max-width:768px) {
    .topBar__menu{
        display: block;
    }
}

@media screen and (max-width:576px) {
    .btn-fullscreen{
        display: none;
        visibility: hidden;
    }
}