.search-wrapper {
    border-radius: 30px;
    background-color: #F8F8F8;
    max-width: 300px;
    padding: 8px 10px;
    display: flex;
    align-items: center;
    transition: all 0.3s linear;
}

.topbar__search {
    background: none;
    border: none;
    display: block;
    width: 100%;
    border-radius: 8px;
    transition: all 0.4s linear;
}

.btn-search {
    background: none;
    border: none;
}

.btn-search:hover {
    color: #F18024;
}

.topbar__search:focus {
    outline: none;
}

@media screen and (max-width:768px) {
    .topbar__search {
        padding: 0;
        max-width: 0;
    }
    .search-wrapper {
        max-width: 220px
    }
    .search-wrapper:hover>.topbar__search {
        max-width: 200px;
    }
    .topbar__search:focus {
        max-width: 200px;
    }
}

@media screen and (max-width:576px) {
    .search-wrapper {
        display: none;
        visibility: hidden;
    }
}