.sideNav {
    position: fixed;
    top: 80px;
    left: 0;
    bottom: 0;
    z-index: 98;
    background-color: white;
    width: 100px;
    box-shadow: rgb(0 0 0 / 10%) 0px 5px 10px, rgb(0 0 0 / 10%) 0px 5px 2px;
    padding: 10px 0;
    transition: all 0.2s linear;
}

.list-sideNav {
    padding-left: 0px;
    list-style: none;
}


.SideBar__link {
    text-decoration: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 110px;
    border-bottom: 1px solid rgb(243, 243, 243);
    color: black;
}

.SideBar__link:hover{
    color: #f19f5b;
}


.selected{
    color: #F18024
}



.item-list i {
    font-size: 32px;
}

@media screen and (max-width:768px) {
    .sideNav{
        transform: translate(-110px, 0);
        z-index: 99;
    }
    .showSideNav{
        transform: translate(0, 0);
    }
}