.teacher-page {
    display: grid;
    grid-template-columns: 70% 30%;
    grid-template-rows: auto auto;
}

.teachers__header {
    display: flex;
    justify-content: space-between;
    grid-column: 1/3;
    grid-row-start: 1;
}

.btn-addTeacher {
    font-weight: 500;
    padding: 0 30px;
}

.table-teacher {
    border-spacing: 0 10px;
    border-collapse: separate;
    grid-row: -2/-1;
}

.table-teacher td {
    background-color: white;
    padding: 10px;
    vertical-align: middle;
}

.RowTeacher {
    border-radius: 10px;
    box-shadow: 0 1px 15px rgb(0, 0, 0, 5%), 0 1px 6px rgb(0, 0, 0, 5%);
}

.EmptyTeachers {
    color: #f18024;
    padding: 20px;
    background-color: rgba(241, 128, 36, .2);
    border-color: rgba(241, 128, 36, .1);
}

@media screen and (max-width:992px) {
    .teacher-page {
      
        grid-template-columns: 1fr;
        
    }
}